.navbar {
    grid-area: nav;
    background-color: #eee;
    padding: 15px;
}

.item {
    margin-bottom: 5px;
}

.item a {
    color: #333;
    text-decoration: none;
    margin-left: 5px;
}

.item a.active {
    font-weight: bold;
}

.item .icon {
    float: left;
    width: 20px;
    color: #333;
}